@import 'normalize',
'vars',
'base';

.river {
  min-height: 100%;
  max-height: 800px;
  background-image: url(https://res.cloudinary.com/jcrosenthal/image/upload/v1408050546/river_yxpscl.jpg);
  background-size: cover;
  background-position: center top;
  display: flex;
  flex-flow: column;

  header {
    flex-grow: 0;
  }

  .name-hero {
    flex-grow: 1;
    display: flex;
    flex-flow: column;
    justify-content: center;
  }
}

.river.fourohfour {
  background-image: url(https://res.cloudinary.com/jcrosenthal/image/upload/v1408050625/404_cjvi7s.gif);
}

a.menu {
  font-family: $open_sans;
  font-size: 16px;
  font-weight: 600;
  line-height: 120%;
  color: rgb(255, 255, 255);
  text-align: right;
  position: relative;
  left: 30px;
  border: solid 1px #FFF;
  padding: 7px 10px;

  @include breakpoint(phone) {
    left: 0;
  }
}

.name-hero {
  span {
    display: block;
    text-align: center;
    color: rgb(255, 255, 255);
    font-family: $merriweather;
    line-height: 120%;
  }
}

.my-name-is {
  font-size: 21px;
  font-style: italic;
  padding-bottom: 10px;
  margin-bottom: 0;
}

.portfolio-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 30px;
  grid-row-gap: 30px;

  img {
    width: 100%;
  }
}

#root {
  height: 100%;
}

.name {
  font-size: 71px;
  margin-top: 0;

  @include breakpoint(phone) {
    font-size: 51px;
  }
}

.subtitle {
  font-size: 16px;
  font-style: italic;
  line-height: 140%;

  a {
    color: white;
    text-decoration: underline;
    position: relative;
    top: 10px;
  }
}

.portfolio-sections {
  background-color: rgb(255, 255, 255);
  width: 100%;
  height: 120px;
  -moz-box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.22);
  -webkit-box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.22);
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.22);
  -ms-filter: "progid:DXImageTransform.Microsoft.dropshadow(OffX=2,OffY=2,Color=#38000000,Positive=true)";
  filter: progid:DXImageTransform.Microsoft.dropshadow(OffX=2, OffY=2, Color=#38000000, Positive=true);

  div.section-link {
    width: 33.33%;
    height: 135px;
    float: left;
    position: relative;
    top: -7px;

    span {
      display: block;
      text-transform: uppercase;
      font-family: $open_sans;
      font-size: 16px;
      line-height: 120%;
      color: rgb(83, 83, 83);
      text-align: center;
      margin-top: 55px;

      @include breakpoint(phone) {
        font-size: 14px;
        word-break: break-word;
      }
    }

    &.active {
      background-color: rgba(52, 210, 152, 0.8);

      span {
        color: #FFF;
      }
    }
  }
}

.portfolio-samples {
  padding: 20px;
  overflow: auto;
  clear: both;

  li {
    transition: all 0.5s linear;

    @include breakpoint(phone) {
      width: 50%;
    }

    img {
      margin: 0 auto;
      display: block;
      transition: all 1s ease;

      &:hover {
        filter: blur(5px);
      }

    }

    &.hide {
      transition: all 0.5s linear;
    }
  }
}

.hide {
  display: none !important;
}

.about-me {
  background-color: #21A8EC;
  overflow: hidden;
  clear: both;
  padding: 90px 30px;

  .row {
    display: flex;
  }

  .copy {
    padding-right: 50px;
  }

  p {
    color: rgb(255, 255, 255);
  }

  h2 {
    font-family: $merriweather;
    font-size: 40px;
    font-weight: bold;
    line-height: 120%;
    color: rgb(255, 255, 255);
    text-align: left;

    @include breakpoint(phone) {
      font-size: 30px;
    }
  }

  img {
    width: 314px;
    height: 314px;
    border-radius: 600px;

    @include breakpoint(phone) {
      max-width: 80%;
      top: 20px;
      left: -30px;
      position: relative;
      height: auto;
    }
  }
}

figure {
  margin: 0;

  figcaption {
    margin-top: -60px;
  }
}

